//CRIANDO OS MIXINS RESPONSIVEIS
@mixin tablet {
  @media only screen and (min-width: 788px) and (max-width: 1194px) {
    @content;
  }
}

@mixin smartphone {
  @media only screen and (max-width: 787px) {
    @content;
  }
}

@mixin smartphonePercent {
  @media only screen and (max-width: 787px) {
    @content;
  }
}

