  background-image: url("data:image/svg+xml,%3Csvg fill='%233B718F' xmlns='http://www.w3.org/2000/svg' viewBox='0 0 48 48' width='48px' height='48px'%3E%3Cpath d='M 6 9 A 2.0002 2.0002 0 1 0 6 13 L 42 13 A 2.0002 2.0002 0 1 0 42 9 L 6 9 z M 6 22 A 2.0002 2.0002 0 1 0 6 26 L 42 26 A 2.0002 2.0002 0 1 0 42 22 L 6 22 z M 6 35 A 2.0002 2.0002 0 1 0 6 39 L 42 39 A 2.0002 2.0002 0 1 0 42 35 L 6 35 z'/%3E%3C/svg%3E");
  width: 10px;
  height: 10px;
}

//CRIANDO OS GRIDS

//GRID EM PORCENTAGEM
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.containerPercent {
  width: 100%;
  margin: 0 auto;
  padding: 0px;
  position: relative;
}

.containerPercent:after,
.containerPercent:before {
  content: " ";
  display: table;
}

.containerPercent:after {
  clear: both;
}

.grid-percent-1,
.grid-percent-2,
.grid-percent-3,
.grid-percent-4,
.grid-percent-5,
.grid-percent-6,
.grid-percent-7,
.grid-percent-8,
.grid-percent-9,
.grid-percent-10,
.grid-percent-1-3 {
  float: left;
  margin-left: 10px;
  margin-right: 10px;
}

$container: 100%;
$gutter: 2%;
$colunas: 10;

@for $i from 1 through $colunas {
  $width: $container / $colunas * $i - $gutter;

  .grid-percent-#{$i} {
    width: $width;
  }
}

$container: 100%;

@include smartphonePercent {
  .container {
    width: 100%;
  }

  @for $i from 1 through $colunas {
    .grid-percent-#{$i} {
      width: 97%;
      float: none;
      margin: 0;
    }
  }
}

//GRID EM PX
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.container {
  width: 960px;
  margin: 40px auto;
  padding: 0px;
  position: relative;
}

.container:after,
.container:before {
  content: " ";
  display: table;
}

.container:after {
  clear: both;
}

.grid-1,
.grid-2,
.grid-3,
.grid-4,
.grid-5,
.grid-6,
.grid-7,
.grid-8,
.grid-9,
.grid-10,
.grid-11,
.grid-12,
.grid-13,
.grid-14,
.grid-15,
.grid-16,
.grid-1-3 {
  float: left;
  margin-left: 15px;
  margin-right: 15px;
}

$container: 960px;
$gutter: 30px;
$colunas: 16;

@for $i from 1 through $colunas {
  $width: $container / $colunas * $i - $gutter;

  .grid-#{$i} {
    width: $width;
  }
}

.grid-1-3 {
  width: 290px;
  margin-bottom: 50px;
}

//TABLET
$container: 768px;

@include tablet {
  .container {
    width: 768px;
  }

  @for $i from 1 through $colunas {
    $width: $container / $colunas * $i - $gutter;

    .grid-#{$i} {
      width: $width;
    }
  }
}

//SMARTPHONE
$container: 300px;

@include smartphone {
  .container {
    width: 300px;
  }

  @for $i from 1 through $colunas {
    .grid-#{$i} {
      width: 300px;
      float: none;
      display: inline;
    }
  }
}

