//---------- CONTATO ----------
.formulario {
  form {
    display: block;
    margin: 20px auto;
    width: 700px;

    label {
      font-size: 16px;
      color: #000;

      @include smartphone {
        &:first-of-type {
          margin-left: -13px;
        }
      }
    }

    input {
      display: block;
      margin-bottom: 15px;
      border: solid 1px #4fb4de;
      width: 89%;
      border-radius: 4px;
    }

    textarea {
      border: solid 1px #4fb4de;
      width: 89%;
      border-radius: 4px;
